    box-sizing: border-box;
    font-family: 'Raleway';
}

body{
    overflow-y: auto;
    overflow-x: hidden;
}


.temp {
    position: absolute;
    width: 100%;
    height: 100vh;
    /* background: url('../image/temp.gif') no-repeat 50% 50%;
    background-size: cover; */
    background: #bd5050;
    animation: fadeOut 5s linear forwards;
    opacity: 1;
    -webkit-animation: fadeOut 5s linear forwards;
    -moz-animation: fadeOut 5s linear forwards;
    -ms-animation: fadeOut 5s linear forwards;
}

@-webkit-keyframes fadeOut {
    0% {
          opacity: 0;
    }
    40% {
          opacity: 0;
    }
    41% {
          opacity: 1;
    }
    91% {
          opacity: 1;
    }
    92% {
          opacity: 0;
    }
    100% {
          opacity: 0;
    }
}

@keyframes fadeOut {
    0% {
          opacity: 0;
    }
    40% {
          opacity: 0;
    }
    41% {
          opacity: 1;
    }
    91% {
          opacity: 1;
    }
    92% {
          opacity: 0;
    }
    100% {
          opacity: 0;
    }
}

.first-block {
    position: absolute;
    width: 0%;
    height: 100vh;
    background: #272727;
    animation: go-left 4s cubic-bezier(.74, .06, .4, .92) forwards;
    -webkit-animation: go-left 4s cubic-bezier(.74, .06, .4, .92) forwards;
    -moz-animation: go-left 4s cubic-bezier(.74, .06, .4, .92) forwards;
    -ms-animation: go-left 4s cubic-bezier(.74, .06, .4, .92) forwards;
    left: 0;
}

@-webkit-keyframes go-left {
    0% {
          left: 0;
          width: 0%;
    }
    50% {
          left: 0;
          width: 100%;
    }
    100% {
          left: 100%;
          width: 0;
    }
}

@keyframes go-left {
    0% {
          left: 0;
          width: 0%;
    }
    50% {
          left: 0;
          width: 100%;
    }
    100% {
          left: 100%;
          width: 0;
    }
}

.second-block {
    position: absolute;
    width: 0%;
    height: 100vh;
    background: rgb(233, 159, 75);
    animation: go-left 3.2s cubic-bezier(.74, .06, .4, .92) forwards;
    -webkit-animation: go-left 3.2s cubic-bezier(.74, .06, .4, .92) forwards;
    -moz-animation: go-left 3.2s cubic-bezier(.74, .06, .4, .92) forwards;
    -ms-animation: go-left 3.2s cubic-bezier(.74, .06, .4, .92) forwards;

    animation-delay: 3s;
    -webkit-animation-delay: 3s;
    -moz-animation-delay: 3s;
    -ms-animation-delay: 3s;
}

.loader {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
}

.loader ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.loader ul li {
    display: inline-block;
    color: #fff;
    font-family: Poppins;
    font-weight: bolder;
    margin: 0 8px;
    font-size: 24px;
    text-transform: uppercase;
    animation: loadNow 4s ease-in-out forwards;
    -webkit-animation: loadNow 4s ease-in-out forwards;
    -moz-animation: loadNow 4s ease-in-out forwards;
    -ms-animation: loadNow 4s ease-in-out forwards;
    opacity: 0;
    transform: translateX(0px);
    -webkit-transform: translateX(0px);
    -moz-transform: translateX(0px);
    -ms-transform: translateX(0px);
}

@-webkit-keyframes loadNow {
    0% {
          opacity: 0;
          transform: translateX(80px);
          -webkit-transform: translateX(80px);
          -moz-transform: translateX(80px);
          -ms-transform: translateX(80px);
    }
    20% {
          opacity: 1;
          transform: translateX(0px);
          -webkit-transform: translateX(0px);
          -moz-transform: translateX(0px);
          -ms-transform: translateX(0px);
    }
    80% {
          opacity: 1;
          transform: translateX(0px);
          -webkit-transform: translateX(0px);
          -moz-transform: translateX(0px);
          -ms-transform: translateX(0px);
    }
    100% {
          opacity: 0;
          transform: translateX(-80px);
          -webkit-transform: translateX(-80px);
          -moz-transform: translateX(-80px);
          -ms-transform: translateX(-80px);
    }
}

@keyframes loadNow {
    0% {
          opacity: 0;
          transform: translateX(80px);
          -webkit-transform: translateX(80px);
          -moz-transform: translateX(80px);
          -ms-transform: translateX(80px);
    }
    20% {
          opacity: 1;
          transform: translateX(0px);
          -webkit-transform: translateX(0px);
          -moz-transform: translateX(0px);
          -ms-transform: translateX(0px);
    }
    80% {
          opacity: 1;
          transform: translateX(0px);
          -webkit-transform: translateX(0px);
          -moz-transform: translateX(0px);
          -ms-transform: translateX(0px);
    }
    100% {
          opacity: 0;
          transform: translateX(-80px);
          -webkit-transform: translateX(-80px);
          -moz-transform: translateX(-80px);
          -ms-transform: translateX(-80px);
    }
}

.loader ul li:nth-child(1) {
    animation-delay: 1.1s;
    -webkit-animation-delay: 1.1s;
    -moz-animation-delay: 1.1s;
    -ms-animation-delay: 1.1s;
}

.loader ul li:nth-child(2) {
    animation-delay: 1.2s;
    -webkit-animation-delay: 1.2s;
    -moz-animation-delay: 1.2s;
    -ms-animation-delay: 1.2s;
}

.loader ul li:nth-child(3) {
    animation-delay: 1.3s;
    -webkit-animation-delay: 1.3s;
    -moz-animation-delay: 1.3s;
    -ms-animation-delay: 1.3s;
}

.loader ul li:nth-child(4) {
    animation-delay: 1.4s;
    -webkit-animation-delay: 1.4s;
    -moz-animation-delay: 1.4s;
    -ms-animation-delay: 1.4s;
}

.loader ul li:nth-child(5) {
    animation-delay: 1.5s;
    -webkit-animation-delay: 1.5s;
    -moz-animation-delay: 1.5s;
    -ms-animation-delay: 1.5s;
}

.loader ul li:nth-child(6) {
    animation-delay: 1.6s;
    -webkit-animation-delay: 1.6s;
    -moz-animation-delay: 1.6s;
    -ms-animation-delay: 1.6s;
}

.loader ul li:nth-child(7) {
    animation-delay: 1.6s;
    -webkit-animation-delay: 1.7s;
    -moz-animation-delay: 1.7s;
    -ms-animation-delay: 1.7s;
}

