.landing-pic{
    position: absolute;
    top: 20%;
    left: 80%;
    width: 3%;
    height: 60%;
    animation: picEffect 2s 1;
    -webkit-animation: picEffect 2s 1;
    -moz-animation: picEffect 2s 1;
    -ms-animation: picEffect 2s 1;
    opacity: 0;
    
    animation-delay: 6s;
    -webkit-animation-delay: 6s;
    -moz-animation-delay: 6s;
    -ms-animation-delay: 6s;
    background: rgb(99, 99, 99);
    z-index: -2;
}

