nav{
    padding: 8px;
    position: fixed;
    display: flex;
    width: 100%; 
    z-index: 9999;
    opacity: 0;
    background:#fff;
}

.logo{
    float:left;
    width: 40%;
    padding: 8px;
    margin-left: 16px;
    margin-top: 8px;
}

.logo a{
    color: #000;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 18px;
    letter-spacing: 0px;
    text-decoration: none;
}

nav .nav-wrapper{
    margin-top: 16px;
    width: 100%;
}

.link{
    transition: 1s;
    -webkit-transition: 1s;
    -moz-transition: 1s;
    -ms-transition: 1s;
}

.link:hover{
    color: rgb(107, 73, 170);
}

nav ul{
    float: right;
}

nav ul li{
    display: inline-block;
}

nav ul li:not(:first-child){
    margin-left: 48px;
}

nav ul li:last-child{
    margin-right: 24px;
}

nav ul li a {
    display: inline-block;
    outline: none;
    color: #000;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 14px;
    letter-spacing: 1.2px;
    font-weight: 600;
    cursor: default;
}

#nav:checked + .nav-btn{
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
}

#nav:checked + .nav-btn i{
    background: #000;
    -webkit-transition: -webkit-transform .2s ease;
    transition: -webkit-transform .2s ease;
    transition: transform .2s ease;
    transition: transform .2s ease, -webkit-transform .2s ease;
    -webkit-transition: transform .2s ease;
    -moz-transition: transform .2s ease;
    -ms-transition: transform .2s ease;
}

#nav:checked + .nav-btn i:nth-child(1){
    transform: translateY(6px) rotate(180deg);
    -webkit-transform: translateY(6px) rotate(180deg);
    -moz-transform: translateY(6px) rotate(180deg);
    -ms-transform: translateY(6px) rotate(180deg);
}

#nav:checked + .nav-btn i:nth-child(2){
    opacity: 0;
}

#nav:checked + .nav-btn i:nth-child(3){
    transform: translateY(-6px) rotate(90deg);
    -webkit-transform: translateY(-6px) rotate(90deg);
    -moz-transform: translateY(-6px) rotate(90deg);
    -ms-transform: translateY(-6px) rotate(90deg);
}

#nav:checked ~ .nav-wrapper{
    z-index: 9990;
    opacity: 1;
    visibility: visible; /* Active when clicking the burger in mobile */
}

#nav:checked ~ .nav-wrapper ul li a{
    opacity: 1;
    transform: translateX(0);
    -webkit-transform: translateX(0);
    -moz-transform: translateX(0);
    -ms-transform: translateX(0);
}

.hidden{
    display: none;
}

@media screen and (min-width: 1600px){
    .logo a{
        font-size: 30px;
    }

    nav ul li a{
        font-size: 30px;
    }
}

@media screen and (max-width: 864px){
    .logo{
        padding: 0;
    }

    .nav-wrapper{
        visibility: hidden; /* Deative until clicking the burger in mobile */
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        background: #fff;
        opacity: 0;
        transition: all .2s ease;
        -webkit-transition: all .2s ease;
        -moz-transition: all .2s ease;
        -ms-transition: all .2s ease;
    }

    .nav-wrapper ul{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        -webkit-transform: translateY(-50%);
        -moz-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
        width: 100%;
    }

    .nav-wrapper ul li{
        display: block;
        float: none;
        width: 5%;
        text-align: right;
        margin-bottom: 10px;
    }

    .nav-wrapper ul li:nth-child(1) a{
        transition-delay: .2s;
        -webkit-transition-delay: .2s;
        -moz-transition-delay: .2s;
        -ms-transition-delay: .2s;
    }

    .nav-wrapper ul li:nth-child(2) a{
        transition-delay: .3s;
        -webkit-transition-delay: .3s;
        -moz-transition-delay: .3s;
        -ms-transition-delay: .3s;
    }

    .nav-wrapper ul li:nth-child(3) a{
        transition-delay: .4s;
        -webkit-transition-delay: .4s;
        -moz-transition-delay: .4s;
        -ms-transition-delay: .4s;
    }

    .nav-wrapper ul li:nth-child(4) a{
        transition-delay: .5s;
        -webkit-transition-delay: .5s;
        -moz-transition-delay: .5s;
        -ms-transition-delay: .5s;
    }

    .nav-wrapper ul li:not(:first-child){
        margin-left: 0;
    }

    .nav-wrapper ul li a{
        padding: 10px 24px;
        opacity: 0;
        color: #000;
        font-size: 14px;
        font-weight: 600;
        letter-spacing: 1.2px;
        transform: translateX(-20px);
        -webkit-transform: translateX(-20px);
        -moz-transform: translateX(-20px);
        -ms-transform: translateX(-20px);
        transition: all .2s ease;
        -webkit-transition: all .2s ease;
        -moz-transition: all .2s ease;
        -ms-transition: all .2s ease;
    }

    .nav-btn{
        position: fixed;
        right: 10px;
        top: 5px;
        display: block;
        width: 48px;
        height: 48px;
        cursor: pointer;
        z-index: 9999;
        border-radius: 50%;
    }

    .nav-btn i{
        display: block;
        width: 20px;
        height: 2px;
        background: #000;
        border-radius: 2px;
        margin-left: 14px;
    }

    .nav-btn i:nth-child(1){
        margin-top: 16px;
    }

    .nav-btn i:nth-child(2){
        margin-top: 4px;
        opacity: 1;
    }

    .nav-btn i:nth-child(3){
        margin-top: 4px;
    }
}
