    -webkit-animation: rev-block 1.5s cubic-bezier(0.19, 1, 0.22, 1) forwards;
    -moz-animation: rev-block 1.5s cubic-bezier(0.19, 1, 0.22, 1) forwards;
    -ms-animation: rev-block 1.5s cubic-bezier(0.19, 1, 0.22, 1) forwards;

    animation-delay: 3.2s;
    -webkit-animation-delay: 3.2s;
    -moz-animation-delay: 3.2s;
    -ms-animation-delay: 3.2s;
}

